@import url(https://fonts.googleapis.com/css?family=Libre+Baskerville);
@import url(https://fonts.googleapis.com/css?family=Lora);
#app {
  padding-top: 40px; }

.loading-screen {
  position: fixed;
  top: 0px;
  right: 0px;
  left: 0px;
  bottom: 0px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: white;
  z-index: 100;
  float: top;
  -webkit-transition: opacity 1s linear 5s, z-index 9s;
  transition: opacity 1s linear 5s, z-index 9s; }
  .loading-screen.loading-finished {
    opacity: 0.0;
    z-index: -1; }

@-webkit-keyframes loading-screen-animation {
  0% {
    background-color: white; }
  50% {
    background-color: black; }
  100% {
    background-color: white; } }

@keyframes loading-screen-animation {
  0% {
    background-color: white; }
  50% {
    background-color: black; }
  100% {
    background-color: white; } }

.side-navigation-bar {
  background-color: black;
  display: flex;
  width: 27em;
  height: 100%;
  float: left;
  overflow: auto;
  position: fixed;
  right: 0px;
  -webkit-transition: left 1s;
  transition: left 1s;
  top: 48px;
  height: calc(100% - 48px); }
  .side-navigation-bar.open {
    left: 0px; }
  .side-navigation-bar.closed {
    left: -30em; }
  .side-navigation-bar li {
    list-style: none;
    text-decoration: none;
    padding-bottom: 20px; }
    .side-navigation-bar li a {
      color: white;
      text-decoration: none;
      font-family: 'Helvetica Neue','Helvetica Neue Light',Helvetica,Arial,'Lucida Grande',sans-serif;
      font-weight: bold;
      -webkit-transition: color 0.5s;
      transition: color 0.5s; }
      .side-navigation-bar li a:hover {
        color: #828282; }

#side-bar-toggle {
